import React from 'react';
import {Group, List, Cell, Separator, Div, Avatar, Counter, Gallery} from "@vkontakte/vkui"
import Icon16Like from '@vkontakte/icons/dist/16/like';
import Icon16LikeOutline from '@vkontakte/icons/dist/16/like_outline';
import {BACKEND} from '../func/func';

class MastersCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeMasterId: this.props.activeMasterId,
            activeMaster: {
                priceList: []
            },
            favsArr: [
                {vkUid:12523452}
            ],
            isFavourite: {
                status: false
            }
        };
    }
    componentWillMount() {
        console.log(this.props);
        this.setState({activeMaster: this.props.activeMaster});
        this.loadFavs();
    }

    loadFavs = () => {
        console.log(BACKEND.favs.master+this.props.activeMaster._id);
        fetch(BACKEND.favs.master+this.props.activeMaster._id)
            .then(res => res.json())
            .then(favsArr => {
                this.setState({favsArr: favsArr});
                let count = favsArr.length;
                this.setState({countFavs: count});
                this.state.favsArr.map(fav => {
                    if (fav.userId === this.props.user._id) {
                        this.setState({isFavourite: {status: true, id: fav._id}});
                        console.log('У тебя в избранном');
                    } else {
                        this.setState({isFavourite: {status: false}})
                        console.log('Нет в избранном');
                    }
                });
            });
    }
    changeVisible = (index) => {
        this.setState({[index]: !this.state[index]})
    }
    checkFavs = () => {
        console.log('click');
        if (this.state.isFavourite.status === false) {
            console.log('post');
            let fav = {
                userId: this.props.user._id,
                userVkUid: this.props.user.vkUid,
                masterId: this.state.activeMaster._id,
                masterVkUid: this.state.activeMaster.vkUid
            };
            this.postData(BACKEND.favs.new, fav, 'POST');
            this.setState({isFavourite: {status: true}});
            this.setState({countFavs: this.state.countFavs+1});
        }

    };
    postData(url = '', data = {}, method) {
        // Значения по умолчанию обозначены знаком *
        return fetch(url, {
            method: method, // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(data), // тип данных в body должен соответвовать значению заголовка "Content-Type"
        })
            .then(response => console.log(response.json())); // парсит JSON ответ в Javascript объект
    }
    render(){
        return (
            <Div>
                <Group title="">
                <Cell
                    photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                    description={this.state.activeMaster.type}
                    bottomContent={<Div style={{display: '-webkit-inline-box'}}>
                        <Icon16Like/><Icon16Like/><Icon16Like/><Icon16Like/><Icon16LikeOutline/>
                    </Div>/*<Button>В избранное</Button>*/}
                    before={<Avatar src={this.state.activeMaster.avatarLink} size={80}/>}
                    size="l"
                >
                    {this.state.activeMaster.firstname} {this.state.activeMaster.lastname}
                </Cell>
            </Group>
            <Group title="">
                <List>
                    <Cell expandable onClick={() => this.props.activePanelMasters('masterComments')} indicator={'32'}>Отзывы</Cell>
                    {
                        this.state.isFavourite.status === false &&
                        <Cell
                            onClick={this.checkFavs}
                            before={<Icon16LikeOutline fill="red" />}
                            description="для получения быстрого доступа к мастеру"
                        >
                            Подписаться
                            {
                                this.state.favsArr.map(fav => {
                                    return console.log(fav.userVkUid)
                                })
                            }
                        </Cell>

                    }
                    {
                        this.state.isFavourite.status &&
                            <Cell
                                description="отменить подписку можно в ЛК"
                                before={<Icon16Like fill="var(--blue)"/>}
                            >Вы подписаны на мастера</Cell>

                    }
                    {/*<UsersStack
                            photos={[
                                'https://sun9-1.userapi.com/c850624/v850624456/9f63e/c2_IbBit7I8.jpg?ava=1',
                                'https://sun9-6.userapi.com/c851528/v851528416/e0360/1UfQ8aSIGVA.jpg?ava=1'
                            ]}
                            size="s"
                        >Настя и Jean подписаны на мастера</UsersStack>*/}
                        <Cell><Counter mode="primary">Подписчиков: {this.state.countFavs}</Counter></Cell>
                </List>
            </Group>
                <Group>
                    <Cell multiline title="Информация">
                        {this.state.activeMaster.description}
                    </Cell>
                </Group>
                <Group>
                        {
                            this.state.activeMaster.priceList.map((item, index) => (
                                    <Cell
                                        key={index}
                                        multiline
                                        onClick={() => this.changeVisible(index)}
                                    >
                                        <Cell
                                            description="Название процедуры"
                                            expandable
                                            indicator={'От '+item.price}>{this.state.activeMaster.priceList[index].title}
                                        </Cell>
                                        {
                                            this.state[index] &&
                                            <Cell description="Краткое описание процедуры" multiline>{this.state.activeMaster.priceList[index].body}</Cell>
                                        }
                                        <Separator></Separator>
                                    </Cell>
                                )
                            )}
                </Group>
                <Group title="Портфолио">
                    <Gallery
                        slideWidth="90%"
                        align="center"
                        style={{ height: 250 }}
                    >
                        <div style={{ backgroundImage: 'url(https://i.pinimg.com/474x/9a/57/f0/9a57f0e84191e278d840a5536ebab34c.jpg)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(https://avatars.mds.yandex.net/get-zen_doc/1554513/pub_5d77a5dd74f1bc00ad79c9f1_5d77a5f198930900ae483c74/scale_1200)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(https://womans.ws/wp-content/uploads/2019/10/1523527373_44-1068x1068.jpg)', backgroundSize: 'cover' }} />
                    </Gallery>
                    <Cell expandable onClick={() => this.props.activePanelMasters('masterPhoto')}>Посмотреть все работы</Cell>
                </Group>
                {/*<Group title="Сертификаты">
                    <Gallery
                        slideWidth="90%"
                        align="center"
                        style={{ height: 500 }}
                    >
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                    </Gallery>
                </Group>*/}
            </Div>
        );
    }
}

export default MastersCard;